import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import Login from "@/pages/login";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: "/",
      redirect: "/login",
    },
    {
      path: "/login",
      name: "Login",
      component: Login,
    },
    {
      path: "/index",
      name: "index",
      component: index,
      redirect: "/index/deploy",
      children: [
        {
          path: "preview",
          name: "preview",
          component: () => import("@/pages/preview/preview"),
        },
        {
          path: "deploy",
          name: "deploy",
          component: () => import("@/pages/deploy/deploy"),
        },
      ],
    },
  ],
});



