<template>
  <div class="home">
    <el-container>
      <el-header
        ><header-title :staffInfor="staffInfor"></header-title
      ></el-header>
      <el-main>
        <router-view />
      </el-main>
    </el-container>
  </div>
</template>

<script>
import headerTitle from "@/components/header";
export default {
  props: [],
  data() {
    return {
      staffInfor: {}
    };
  },
  computed: {},
  created() {},
  mounted() {
    this.getStaffInfor();
  },
  watch: {},
  methods: {
    getStaffInfor() {
      this.$axios.post("/data/getStaffInforByToken").then(res => {
        // console.log("StaffInfor", res);
        this.staffInfor = res.data;
      });
    }
  },
  components: {
    headerTitle
  }
};
</script>

<style scoped lang="less">
@variable: 40px;
.home {
  .el-container {
    .el-header {
      padding: 0;
      height: @variable !important;
      background-color: #2d2d2d;
    }
    .el-main {
      height: calc(100vh - @variable);
      padding: 0;
    }
  }
}
</style>



