<template>
  <div class="header">
    <div class="logo">
      <img src="../../static/logo.png" alt="" />
      <span>千湖视觉</span>
    </div>
    <div class="mune">
      <ul>
        <router-link
          :to="item.path"
          v-for="(item, index) in allPath"
          :key="index"
          ><li :class="{ active: path == item.path }">
            {{ item.name }}
          </li></router-link
        >
      </ul>
    </div>
    <div class="user">
      <ul>
        <li><i class="el-icon-user-solid"></i> {{ staffInfor.staffName }}</li>
        <li @click="loginOut">
          <i class="el-icon-switch-button"></i>
          退出
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ["staffInfor"],
  data() {
    return {
      path: "",
      allPath: [
        { path: "/index/preview", name: "预览" },
        { path: "/index/deploy", name: "配置" }
      ]
    };
  },
  computed: {},
  created() {},
  mounted() {
    this.path = this.$route.path;
    // console.log("path", this.path);
  },
  watch: {
    $route: {
      handler: function(val, oldVal) {
        this.path = val.fullPath;
        // console.log("path1", val.fullPath);
      }
    }
  },
  methods: {
    loginOut() {
      sessionStorage.removeItem("token");
      console.log();
      if (window.sessionStorage.getItem("token") == null) {
        this.$message("正在退出登录...");
        setTimeout(() => {
          this.$router.replace("/login");
        }, 1000);
      }
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.header {
  height: 100%;
  display: flex;
  .logo {
    flex: 1;
    // background-color: pink;
    display: flex;
    align-items: center;
    box-sizing: border-box;

    img {
      width: 35px;
      height: 30px;
      padding-left: 10px;
    }
    span {
      color: white;
      font-size: 18px;
      font-weight: 800;
      margin-left: 20px;
    }
  }
  .mune {
    flex: 5;
    ul {
      height: 100%;
      display: flex;
      li {
        width: 120px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        color: white;
        font-size: 14px;
        &:hover {
          background-color: red;
        }
      }
      .active {
        background-color: red;
      }
    }
  }
  .user {
    flex: 1;
    box-sizing: border-box;
    ul {
      display: flex;
      align-items: center;
      justify-content: space-around;
      width: 100%;
      height: 100%;
      padding: 0 20px;
      box-sizing: border-box;
      li {
        font-size: 14px;
        color: #ccc;
        cursor: pointer;

        i {
          // color: #ccc;
        }
      }
    }
  }
}
</style>



