<template>
  <div class="login_content">
    <div>
      <el-form ref="formData" :model="formData">
        <el-form-item label="用户名" prop="account">
          <el-input
            v-model="formData.account"
            @keyup.enter.native="onSubmit"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input
            v-model="formData.password"
            show-password
            @keyup.enter.native="onSubmit"
          ></el-input>
        </el-form-item>
        <el-form-item class="buttons">
          <el-button type="primary" @click="onSubmit" round>登录</el-button>
          <el-button type="info" round @click="resetForm('formData')"
            >重置</el-button
          >
        </el-form-item>
        <!-- <el-checkbox v-model="checked" @change="keepChecked">是否记录状态</el-checkbox> -->
        <!-- <el-button type="info" round @click="test">test</el-button> -->
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  props: [],
  data() {
    return {
      formData: {
        account: "admin",
        password: "123456"
      }
    };
  },
  computed: {},
  created() {},
  mounted() {},
  watch: {},
  methods: {
    onSubmit() {
      this.$axios.post(`login`, this.$qs.stringify(this.formData)).then(res => {
        console.log("登录接口", res);
        if (res.data.status == 200) {
          this.$message.success(res.data.message);
          this.$router.push("/index");

          window.sessionStorage.setItem("token", res.headers.token);
        } else {
          this.$message.error(res.data.message);
        }
      });
    },
    resetForm(formData) {
      this.$refs[formData].resetFields();
    },
  },
  components: {}
};
</script>

<style scoped lang="less">
.login_content {
  color: red;
  height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  .el-form {
    margin: 0 auto;
    width: 400px;
    color: red;
    padding: 50px 50px 30px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);

    .buttons {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      button {
        width: 45%;
      }
    }
  }
  .el-checkbox {
    display: block;
    margin-top: 40px;
    display: flex;
    justify-content: center;
  }
}
</style>



